export default {
	data() {
		return {
			tabs: [],
			content: []
		};
	},
	filters: {
		formDate(time){
			const d = new Date(time);
			const datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':0' + d.getMinutes() + ':0' + d.getSeconds();
			return datetime
		}
	},
	onLoad() {
		this.initTabs(data => {
			// 默认第一个选项
			this.getContent(data[0].id);
		});
	},
	methods: {
		// 获取标签页
		initTabs(callback) {
			const _this = this;
			_this.$api
				.tablist()
				.then(res => {
					if (+res.code === 1) {
						const { data } = res;
						_this.tabs = data;
						callback(data);
					}
				})
				.catch(res => {
					console.log(res);
				});
		},
		// 获取内容
		getContent(id) {
			const _this = this;
			_this.$api
				.tabForContent({ id: id })
				.then(res => {
					if (+res.code === 1) {
						const { data } = res;
						_this.content = data;
					}
				})
				.catch(res => {
					console.log(res);
				});
		}
	}
};
